* {
    margin: 0;
    scroll-behavior: smooth;
  }

  
  /* Chrome, Safari, Edge, Opera */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  

  .greenColor {
    color: green !important;
  }
  .redColor {
    color: red !important;
  }
